@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Poppins:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: inherit;
  color: inherit;
}
body{
  color: #e2e2e2;
  overflow-x: hidden;
  font-family: "Montserrat",sans-serif;
  font-size: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
}
.App{
  background-color: #101427;
  width: 670px;
  height: 570px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.Home-content{
    min-height: 450px;
    height: -webkit-min-content;
    height: min-content;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
  .Home-content__title{
    margin-left: 52px;
    font-size: 30px;;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    text-align: center;
    color: #c6c8cf;

}

.Home-content__select{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px;
  width: 100%;
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.Home-content__select .MuiInputLabel-outlined{
  color: #767c96;
}
.Home-content__select .MuiOutlinedInput-input{
  color: #c6c8cf;
  font-weight: bold;
  cursor: pointer;
}


 @media (max-width: 667px) {
    .Home-content {
      -webkit-flex-direction: column-reverse;
              flex-direction: column-reverse;
      -webkit-align-items: center;
              align-items: center;
    }
    
    .Home-content__title {
      width: 100%;
    }
}


.question {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.singleQuestion {
  width: 95%;
  min-height: 350px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border: 5px solid grey;
  padding: 20px;
  margin-top: 10px;
}

.options {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 10px;
}

.singleOption {
  width: 46%;
  height: 50px;
  padding: 15px 20px;
  margin: 10px;
  box-shadow: 0 0 2px black;
}

@media (max-width: 614px) {
  .singleOption {
    width: 100%;
  }
}

.controls {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.select {
  background-color: rgb(7, 207, 0);
  color: white;
  box-shadow: 0 0 1px black;
}

.wrong {
  background-color: rgb(233, 0, 0);
  color: white;
  box-shadow: 0 0 1px black;
}

.Quiz-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-family: "Poppins", sans-serif;
  }
  
  .Quiz-container__subtitle {
    font-size: 18px;
    padding: 5px 10px;
    margin-bottom: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: #c6c8cf;

  }
 
  .Quiz-container__info {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 10px;
    font-size: 16px;
    /* color: #767c96; */
    color: #c6c8cf;
  }
  .question h1{
    color: #666D91;
  }
  .singleQuestion {
    border:none;
  }
  .singleQuestion h2{
      font-size: 18px;
  }
  .question h1{
      font-size: 25px;
  }
  .controls .MuiButton-containedSecondary{
    border-radius: 5px;
    transition: all 0.2s ease;
    background: #E1332D;
    color: #fff;
    cursor: pointer;
  }
  .controls .MuiButton-containedSecondary:hover{
    background: #e05955;
  }  
  .options button{
    border-radius: 5px;
    border: none;
    cursor: pointer;
  }
  .options .select {
    background-color: #189541;
  }
  .options .wrong{
    background: #E1332D;
  }
  
  .controls .MuiButton-containedPrimary{
    border-radius: 5px;
    background-color: #189541;
    color: #fff;
    transition: all 0.2s ease;
  }
  .controls .MuiButton-containedPrimary:hover{
    background: #3da760;
    color: #fff;
  }
